.calculator {
  padding-top: 95px;
  width: 263px;
  position: relative;
  background-color: var(--main-calculator-bg-color);
  border-radius: 10px;
  box-shadow: #00000060 0px 1px 12px 0px;
}

.calculator__buttons {
  display: grid;
  background-color: var(--buttons-border-color);
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
  grid-gap: 1px 1px;
  gap: 1px 1px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
}
.calculator__previous-result {
  color: var(--equals-button-bg-color);
  margin-right: 33px;
  font-size: 18px;
  text-align: right;
  margin-bottom: 5px;
  height: 21px;
}
.error-expression {
  color: rgba(255, 0, 0, 0.644);
  font-size: 17px;
  font-weight: 700;
  text-align: center;
  position: absolute;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  top: 70px;
  -webkit-animation: show-error-expression 0.5s forwards;
          animation: show-error-expression 0.5s forwards;
}
@-webkit-keyframes show-error-expression {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes show-error-expression {
  0% {
    opacity: 0;
  }
