.toggle {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  margin-bottom: 15px;
  margin-left: 210px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-item {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.toggle-item:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: var(--equals-button-font-color);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .toggle-item {
  background-color: var(--equals-button-bg-color);
}

input:checked + .toggle-item:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.toggle-item.toggle-round {
  border-radius: 34px;
}

.toggle-item.toggle-round:before {
  border-radius: 50%;
}
